.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  @media only screen and (max-width: 550px) {
    margin-top: -64px;
  }

  &__section-title {
    @include h1;
    margin-bottom: 24px;
    @media only screen and (max-width: 620px) {
      text-align: center;
    }
  }
}
