.checkout {
  display: flex;
  flex-direction: column;
  height: 70vh;

  &__back-home {
    display: flex;
    align-items: center;
    justify-self: flex-start;
  }

  &__link-btn {
    background: none;
    outline: none;
    padding: 5px 12px;
    font-weight: 700;
  }

  &__message {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 70px ;
    font-size: 30px;
    margin-top: 10vh;
    background-color: #e5e5e5;
    border-radius: 0;
    box-shadow: inset 0 0 10px rgba(0,0,0,0.3),
      0 0 10px rgba(0,0,0,0.3);
    max-width: 400px;
    height: 400px;
    opacity: 0.7;
    background-image: url(./parcel.png);
    background-repeat: no-repeat;
    background-size: cover;

  }
}
