/*@use postcss-cssnext;*/

:root {
  --birds-dark: #878787;
  --birds-medium-dark: #afafaf;
  --birds-medium-light: #d3d3d3;
  --birds-light: #e2e2e2;
  --birds-bright: #ff5629;
  --birds-shadow: #ddd;
}

.no-products {
  position: relative;
  bottom: -60px;
  text-align: center;
  @media only screen and (max-width: 620px) {
    bottom: 0;
  }

  &__title {
    display: block;
    @include h2;
    font-size: 2em;
    color: $c-extra-text;
    margin-bottom: 60px;
    text-shadow: 1px 1px 0 #1d1d1b;

    @media only screen and (max-width: 620px) {
      @include h3;
      margin: 0 15px;
    }
  }
}

.birds {
  position: relative;
  overflow: hidden;
  width: 800px;
  height: 500px;
  margin: auto;
  @media only screen and (max-width: 620px) {
    width: auto;
  }

  &__hatdove {
    position: absolute;
    top: 70px;
    left: 450px;
    width: 255px;
    height: 400px;
  }
  &__hatdove-head {
    position: absolute;
    top: 0;
    left: 80px;
    width: 80px;
    height: 190px;
    border-radius: 40px 40px 0 0;
    background: var(--birds-dark);
    animation: hatdove-head 2.75s linear infinite;

    &:before {
      content: "";
      position: absolute;
      top: 22px;
      left: -12px;
      width: 42px;
      height: 42px;
      border-radius: 100%;
      background: var(--birds-dark);
    }
  }
  &__hatdove-hat {
    position: absolute;
    top: 10px;
    left: 5px;
    width: 84px;
    height: 22px;
    border-radius: 5px;
    background: #1d1d1b;
    transform: rotate(20deg);

    &:after {
      content: "";
      position: absolute;
      top: -26px;
      left: 4px;
      width: 76px;
      height: 36px;
      border-radius: 34px 34px 0 0;
      background: #1d1d1b;
    }
    &:before {
      content: "";
      position: absolute;
      top: -44px;
      left: 29px;
      width: 24px;
      height: 24px;
      border-radius: 100%;
      background: var(--birds-bright);
    }
  }
  &__hatdove-forehead {
    position: absolute;
    top: 20px;
    left: 4px;
    width: 78px;
    height: 22px;
    border-radius: 100%;
    background: var(--birds-dark);
    transform: rotate(20deg);
  }
  &__hatdove-eye {
    position: absolute;
    top: 26px;
    left: -8px;
    width: 34px;
    height: 34px;
    border-radius: 100%;
    background: white;
    overflow: hidden;
    animation: hatdove-eye 2.75s linear infinite;

    &:after {
      content: "";
      position: absolute;
      left: 11px;
      top: 9px;
      width: 7px;
      height: 22px;
      border-radius: 100%;
      background: black;
      animation: hatdove-pupil 2.75s linear infinite;
    }
  }
  &__hatdove-eye:nth-child(4) {
    left: 20px;
  }
  &__hatdove-beak {
    position: absolute;
    top: 67px;
    left: 10px;
    width: 22px;
    height: 22px;
    border-radius: 100%;
    background: white;

    &:after {
      content: "";
      position: absolute;
      top: 17px;
      left: -13px;
      width: 31px;
      height: 8px;
      border-radius: 1px 0 14px 70px / 1px 0 14px 20px;
      background: var(--birds-bright);
    }
    &:before {
      content: "";
      position: absolute;
      top: 2px;
      left: -28px;
      width: 46px;
      height: 15px;
      border-radius: 30px 14px 0 1px / 14px 13px 0 1px;
      background: var(--birds-bright);
    }
  }
  &__hatdove-body {
    position: absolute;
    top: 174px;
    left: 69px;
    width: 91px;
    height: 110px;
    border-radius: 13px 0 0 40px / 70px 0 0 40px;
    background: var(--birds-medium-light);

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 91px;
      width: 0;
      height: 0;
      border-bottom: 110px solid var(--birds-medium-light);
      border-right: 68px solid transparent;
      border-radius: 0 0 20px 0 / 0 0 40px 0;
    }
    &:before {
      content: "";
      position: absolute;
      top: 40px;
      left: 91px;
      width: 0;
      height: 0;
