.footer {
  justify-content: center;
  display: flex;
  background-color: #fff;
  height: 96px;
  border-top: solid 1px #e2e6e9;
  margin-top: -96px;
  box-sizing: border-box;

  &.fixed {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &__container {
    width: 1400px;
    background-color: #fff;
  }

  &_nav {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
  }

  &__logo {
    display: flex;
    align-self: center;
    width: 48px;
    padding-left: 24px;
  }

  &__nav-list {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: 96px;
    column-gap: 64px;
    align-items: center;
    @media only screen and (max-width: 1060px) {
      display: none;
    }
    &--mini {
      display: grid;
      grid-template-columns: repeat(3, auto);
      grid-template-rows: 96px;
      column-gap: 4vw;
      align-items: center;
      @media only screen and (min-width: 1061px) {
        display: none;
      }
    }
  }

  &__nav-list.nav__icons-list {
    display: none;
  }

  &__link {
    display: flex;
    align-self: center;
    justify-content: flex-end;

    &--up {
      display: flex;
      align-items: center;
      @include small-text;
      color: $c-extra-text;
      cursor: pointer;
      padding-right: 24px;
      & .icon.uparrow {
        position: static;
        transform: rotate(0);
      }
    }
  }

  &__btn-link--up {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border: 1px solid #b4bdc4;
    box-sizing: border-box;
    margin-left: 16px;
  }
}

.nav__footer-link-icon {
  padding: 10px;
  fill: #89939a;
  transition: all 0.1s ease-in;
}

.nav__footer-item:hover {
  .nav__footer-link-icon {
    fill: #313237;
  }
}
