.icon.uparrow {
  transform: rotate(-180deg);
  transition: transform 0.3s;
  position: absolute;
  right: 15px;
  bottom: 12px;
  z-index: 10;
}

.catalog__sort {
  margin-bottom: 24px;
  display: flex;

  @media only screen and (max-width: 960px) {
    justify-content: center;
  }

  &-form {
    display: grid;
    grid-template-columns: 176px 128px;
    column-gap: 16px;

    @media only screen and (max-width: 620px) {
      display: grid;
      width: 272px;
      grid-template-columns: 1fr 1fr;
    }
  }

  &-title {
    display: block;
    @include small-text;
    color: $c-extra-text;
    margin-bottom: 4px;
    @media only screen and (max-width: 960px) {
      text-align: center;
    }
  }

  select {
    padding-right: 25px;
    appearance: none;
    font-family: Mont, sans-serif;
    font-size: 14px;
    line-height: 21px;
    font-weight: 600;
  }

  &-select {
    position: relative;
    width: 100%;
    height: 40px;
    border: 1px solid #b4bdc4;
    box-sizing: border-box;
    padding: 0.6em 1.4em 0.5em 0.8em;
    background-color: #fff;

    &-option {
      color: #89939a;
      border: none;
      outline: none;
    }

    &-option:checked {
      color: #313237;
    }
  }

  &-by-params {
    position: relative;
  }

  &-by-count {
    position: relative;
  }

  &-by-count.active,
  &-by-params.active {
    .icon.uparrow {
      transform: rotate(0);
      fill: #333;
    }

    .catalog__sort-select {
      border-color: #333;
    }
  }
}
